	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Cybercriminals are often perceived as having super powers to get into your systems and steal your money and data. The vast majority are just following simple cookbook recipes to take advantage of laziness, sloppiness and an failure to understand what might be risky behaviour. Defences against cybercrime do not have to be hi-tech or even very complex.\nThis talk discusses various types of cybercrime and gives some practical advice as to how to make yourself a little safer.","meta":{"duration":"PT51M4S"},"presenter":{"name":"Richard Clayton"},"source":{"id":"624S9ly09Xk","provider":"youtube"},"slug":"a-practical-guide-to-cybercrime","tag":["GOTO Conference","University of Cambridge","Cybercrime","Security","Privacy"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/624S9ly09Xk/default.jpg","height":90,"width":120}},"title":"A Practical Guide to Cybercrime"}}]}},"pathContext":{"speaker":"Richard Clayton"}}

